import React, { useState } from "react";
import { AsyncPaginate } from "react-select-async-paginate";
import { geoApiOptions, GEO_API_URL } from "../../api";
import { weatherApp_backend } from "../../../../declarations/weatherApp_backend";
const Search = ({ onSearchChange }) => {
    const [search, setSearch] = useState(null);
    const loadOptions = async (inputValue) => {
        const response = await fetch(`${GEO_API_URL}/cities?minPopulation=1000000&namePrefix=${inputValue}`, geoApiOptions);
        const searching = inputValue;
        const push = await weatherApp_backend.put(searching);
        const listData = await weatherApp_backend.fetchList();
        const userSearch = listData.map(({ name }) => name);
        console.log(userSearch);
        const response_1 = await response.json();
        return {
            options: response_1.data.map((city) => {
                return {
                    value: `${city.latitude} ${city.longitude}`,
                    label: `${city.name}, ${city.countryCode}`,
                };
            }),
        };
    };
    const handleOnChange = (searchData) => {
        setSearch(searchData);
        onSearchChange(searchData);
    };
    return (React.createElement(React.Fragment, null,
        React.createElement(AsyncPaginate, { placeholder: "Search for city", debounceTimeout: 600, value: search, onChange: handleOnChange, loadOptions: loadOptions })));
};
export default Search;
